@media (max-width: 768px) {
  .header__toggle {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 13;
    width: 40px;
    height: 30px;
    padding: 15px 5px 15px 10px;
    top: 10px;
    right: -1px;
    background: rgba(255, 255, 255, 0);
    border: none;
    cursor: pointer; }
  .header__toggle span {
    position: relative;
    display: inline-block;
    width: 24px;
    height: 2px;
    vertical-align: bottom;
    background: #ffffff; }
  .header__toggle span::after,
  .header__toggle span::before {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background: #ffffff;
    transform-origin: 50% 50%;
    transition: top 0.3s, transform 0.3s; }
  .header__toggle span::before {
    top: 8px; }
  .header__toggle span::after {
    top: -8px; }
  .is-open span {
    background: rgba(255, 255, 255, 0); }
  .is-open span::before,
  .is-open span::after {
    width: 100%;
    left: 0;
    top: 0;
    background: #ffffff; }
  .is-open span::before {
    transform: rotate3d(0, 0, 1, 45deg); }
  .is-open span::after {
    transform: rotate3d(0, 0, 1, -45deg); }
  .no-js span {
    display: none; } }

